.episodes {
  height: 100vh;
  background-color: rgba($color: #000000, $alpha: 0.2);
  padding: 20px 20px;

  &__heading {
    text-align: center;
    text-transform: uppercase;
    padding-bottom: 20px;
    border-bottom: 1px solid salmon;
  }

  &__list {
    text-align: center;
    font-size: 25px;
  }

  &__item {
    padding-top: 10px;
  }
}
